import React, { useState } from "react";
import { Row, Col, Button, Card, Table, Tag } from 'antd';
import StatisticWidget from 'components/shared-components/StatisticWidget';
import ChartWidget from 'components/shared-components/ChartWidget';
import AvatarStatus from 'components/shared-components/AvatarStatus';
import GoalWidget from 'components/shared-components/GoalWidget';
import ApexChart from "react-apexcharts";
import { apexLineChartDefaultOption, COLOR_2 } from 'constants/ChartConstant';
import {withRouter} from 'react-router-dom';
import { useSelector } from 'react-redux'
import NumberFormat from 'react-number-format';
import utils from 'utils';
import Flex from 'components/shared-components/Flex'

const getStatus = status => {
	if(status == 'Pending') {
		return <Tag className ="text-capitalize" color="#108ee9">Pending</Tag>
	}
	else if(status == 'En Route') {
		return <Tag className ="text-capitalize" color="#ffc542">En Route</Tag>
	}
	else if(status == 'Delivered') {
		return <Tag className ="text-capitalize" color="#87d068">Delivered</Tag>
	}
	else{
		return <Tag className ="text-capitalize" color='red'>Cancelled</Tag>
	}
}

export const DefaultDashboard = ({systemData}) => {
  const [month, setMonth] = useState(new Date(new Date().getFullYear(), new Date().getMonth
	()).setDate(1))
  
  const { direction } = useSelector(state => state.theme)
  const {avatarColors, totalEstimated, totalClients, sitesArray, sitesClientsArray, recentClients, siteEstimation, paid, paidLoading, account, accountLoading, monthlyPaid, allDestinationsLoading, allDestinations,  allTrucks, borders, bordersLoading, currencies, currenciesLoading, trips, tripsLoading, orders, ordersLoading,  allRentedTrucks, getEnding, expenses, expensesLoading, fuelsLoading, fuels, garages, garagesLoading } = systemData;

  const [totalEstimation] = useState(totalEstimated(month))
  const [thisMonthPaid] = useState(monthlyPaid(month))
  let ending = getEnding();
  let starting = new Date().setDate(1);
  starting = new Date(starting).setHours(0,0,0,0);
  let montlyOrders = ordersLoading ? [] : orders.docs.filter(x=> +x.data()._id >= starting && +x.data()._id <= ending);
  let ordersAmount = montlyOrders.reduce((accumulator, currentElement) => {
    return parseFloat(accumulator) + parseFloat(currentElement.data().totalAmount)
  },0)

  let monthlyTrips = tripsLoading ? [] : trips.docs.filter(x=> +x.data()._id >= starting && +x.data()._id <= ending);

  let montlyMilleage = monthlyTrips.reduce((accumulator, currentElement) => {
    return parseFloat(accumulator) + parseFloat(currentElement.data().milleage)
  },0)

  let monthlyExpenses = expensesLoading ? [] : expenses.docs.filter(x=> +x.data().date >= starting && +x.data().date <= ending);

  let expensesAmount = monthlyExpenses.reduce((accumulator, currentElement) => {
    return parseFloat(accumulator) + parseFloat(currentElement.data().amount)
  },0)

  let monthlyFuels = fuelsLoading ? [] : fuels.docs.filter(x=> +x.data()._id >= starting && +x.data()._id <= ending);

  let fuelsAmount = monthlyFuels.reduce((accumulator, currentElement) => {
    return parseFloat(accumulator) + parseFloat(currentElement.data().totalAmount)
  },0)

  let monthlyGarage = garagesLoading ? [] : garages.docs.filter(x=> +x.data()._id >= starting && +x.data()._id <= ending);

  let garagesAmount = monthlyGarage.reduce((accumulator, currentElement) => {
    return parseFloat(accumulator) + parseFloat(currentElement.data().totalAmount)
  },0)

  let allRecords = [];
  	if(!ordersLoading){
		orders.docs.map((doc)=>{
			allRecords = {
				...allRecords,
				[doc.data()._id] : {'client':doc.data().client, 'item':doc.data().item, 'departure':doc.data().departure, 'destination':doc.data().destination},
			}
		})
	}

  const tableColumns = [
		{
			title: 'Date',
			dataIndex: 'id',
			render: (_,record) => new Date(+record.data()._id).toLocaleString()
		},
		{
			title: 'Truck',
			dataIndex: 'id',
			render: (_,record) => record.data().internal ? 
			`${allTrucks[record.data().truckId]?.plateNumber} - ${allTrucks[record.data().truckId]?.driverName}`
			:
			`${allRentedTrucks[record.data().truckId]?.plateNumber} - ${allRentedTrucks[record.data().truckId]?.driverName}`
		},
		{
			title: 'Milleage',
			dataIndex: 'milleage',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().milleage}
							  suffix=' RWF' 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Quantity',
			dataIndex: 'quantity',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().quantity} 
							  suffix={' ton of ' + allRecords[record.data().orderId]?.item} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Departure',
			dataIndex: 'status',
			render: (_, record) => (
				<Flex alignItems="center">{allDestinationsLoading ? '' : allDestinations[record.data().departure]?.name +' - '+allDestinations[record.data().departure]?.country}</Flex>
			),
		},
		{
			title: 'Destination',
			dataIndex: 'status',
			render: (_, record) => (
				<Flex alignItems="center">{allDestinationsLoading ? '' : allDestinations[record.data().destination]?.name +' - '+allDestinations[record.data().destination]?.country}</Flex>
			),
		},
		{
			title: 'Status',
			dataIndex: 'status',
			render: (_, record) => (
				<Flex alignItems="center">{getStatus(record.data().status)}</Flex>
			),
		}
	];

  return (
    <>  
      <Row gutter={16}>
        <Col xs={24} sm={24} md={24} lg={18}>
          <Row gutter={16}>
            <Col xs={24} sm={24} md={24} lg={8} xl={8}>
              <StatisticWidget 
                title="Orders" 
                value={montlyOrders.length}
              />
            </Col>
            <Col xs={24} sm={24} md={24} lg={8} xl={8}>
              <StatisticWidget 
                title="Orders Amount" 
                value={<NumberFormat
                  displayType={'text'} 
                  value={ordersAmount} 
                  thousandSeparator={true} 
                />}
              />
            </Col>
            <Col xs={24} sm={24} md={24} lg={8} xl={8}>
              <StatisticWidget 
                title="Total milleage"
                value={<NumberFormat
                  displayType={'text'} 
                  value={montlyMilleage} 
                  thousandSeparator={true} 
                />}
              />
            </Col>
            <Col xs={24} sm={24} md={24} lg={8} xl={8}>
              <StatisticWidget 
                title="Expenses Amount" 
                value={<NumberFormat
                  displayType={'text'} 
                  value={expensesAmount} 
                  thousandSeparator={true} 
                />}
              />
            </Col>
            <Col xs={24} sm={24} md={24} lg={8} xl={8}>
              <StatisticWidget 
                title="Fuel Amount" 
                value={<NumberFormat
                  displayType={'text'} 
                  value={fuelsAmount} 
                  thousandSeparator={true} 
                />}
              />
            </Col>
            <Col xs={24} sm={24} md={24} lg={8} xl={8}>
              <StatisticWidget 
                title="Garage service"
                value={<NumberFormat
                  displayType={'text'} 
                  value={garagesAmount} 
                  thousandSeparator={true} 
                />}
              />
            </Col>
          </Row>

        </Col>
        <Col xs={24} sm={24} md={24} lg={6}>
          <GoalWidget 
            title="Total trips" 
            value={monthlyTrips.length}
            subtitle={'This Month Only'}
          />
        </Col>
      </Row>
      <Row gutter={16}>
        <Col xs={24} sm={24} md={24} lg={24}>
          <Card title="Latest trips">
            <Table 
              className="no-border-last" 
              columns={tableColumns} 
              dataSource={!tripsLoading ? trips.docs.reverse().splice(0,5) : []} 
					    loading={tripsLoading}
              pagination={false}
            />
          </Card>
        </Col>
      </Row>
    </>
  )
}


export default withRouter(DefaultDashboard);
